.outer {
  /* works with .App display:inline-block
  to center the content within .App */
  text-align: center;
  width: 80%;
  margin: auto;
}

.App {
  display: inline-block;
  display: flex;
  flex-flow: row wrap;
  text-align: center;
  margin-top: 100px;
  justify-content: center;
  /* width: 80%; */
  margin: auto;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
